import React, { useState, useEffect } from 'react';

import ProductsList from '../../base_components/products_list';
import { WxpayAccountRequestGet } from './wxpay_account_requests';
// import CreateWxpayAccount from './create_wxpay_account';
import UpdateWxpayAccount from './update_wxpay_account';
import { ItemRendererComponent } from './wxpay_account_columns_label_config';

// 列表编辑 页面

const WxpayAccountList = ({ reloadListCount, setReloadListCount, requestParams }) => {
  // const hasMore = useRef(true);
  const [hasMoreState, setHasMoreState] = useState(true);
  const [loading, setLoading] = useState(false);
  const [requesting, setRequesting] = useState(true);
  // const reqParams = useRef({ page: 0 });

  const [wxpayAccountData, setWxpayAccountData] = useState([]);
  const [wxpayAccountRequestGetCount, setWxpayAccountRequestGetCount] = useState(0);

  // const [createWxpayAccountVisible, setCreateWxpayAccountVisible] = useState(false);
  const [updateWxpayAccountVisible, setUpdateWxpayAccountVisible] = useState(false);
  const [selectedObj, setSelectedObj] = useState({});

  useEffect(() => {
    // 加载、更新或创建数据后 重新读取列表
    // reqParams.current = { ...requestParams };
    reloadListsHandle();
  }, [reloadListCount]);

  // useEffect(() => {
  //   // useState才可以读取更多
  //   setHasMoreState(hasMore.current);
  // }, [hasMore.current])

  const loadMore = () => {
    // 读取更多列表
    if (hasMoreState && !loading) {
      // const current_page = Number.isInteger(reqParams.current.page) ? reqParams.current.page + 1 : 1;
      // reqParams.current = { ...reqParams.current, page: current_page };
      // console.log(reqParams.current);
      setWxpayAccountRequestGetCount(wxpayAccountRequestGetCount + 1);
      setLoading(true);
    } else {
      console.log('Error state: hasMore is ', hasMoreState, ', loading is ', loading);
    }
  };

  useEffect(() => {
    // 使用 useEffect 解决 request 请求异步
    if (requesting && hasMoreState && !loading) {
      setRequesting(false);
      loadMore();
    }
  }, [requesting, hasMoreState, loading]);

  const reloadListsHandle = () => {
    // 重新读取数据
    // reqParams.current = { ...reqParams.current, page: 0 };
    // console.log(reqParams.current);
    setWxpayAccountData([]);
    // hasMore.current = true;
    setHasMoreState(true);
    setLoading(false);
    setRequesting(true);
    // setTimeout(() => {
    //   loadMore();
    //   console.log('end loadMore...');
    // }, 100);
  };

  const judgeHasMore = resp => {
    if (Object.keys(resp).includes('next')
      && typeof resp.next === 'string'
      && resp.next.length > 5) {
      return true;
    }
    return false;
  };

  const wxpayAccountGetResponse = resp => {
    console.log('resp process...');
    setLoading(false);
    if (Object.keys(resp).includes('code')
      && resp.code === 1000
      && Object.keys(resp).includes('results')) {
      setWxpayAccountData(wxpayAccountData.concat(resp.results));
      // setHasMore(judgeHasMore(resp));   // 判断是否还有更多
      // hasMore.current = judgeHasMore(resp);
      setHasMoreState(judgeHasMore(resp));
    } else {
      console.log('Error: get data...');
    }
  };

  const itemDetailClick = (index) => {
    // 打开详情编辑窗口
    // console.log(wxpayAccountData[index]);
    let selObj = { ...wxpayAccountData[index] };
    console.log(selObj);
    // 日期转 moment 时间
    // selObj.end_time = moment(dateFormat(selObj.end_time), 'YYYY-MM-DD HH:mm:ss');
    setSelectedObj(selObj);
    setUpdateWxpayAccountVisible(true);
  };

  const CurrentItemRendererComponent = ({ item, index }) => {
    // 每一行渲染 的组件
    return (
      <ItemRendererComponent item={item} index={index} itemDetailClick={itemDetailClick} />
    )
  }

  return (
    <div>
      <WxpayAccountRequestGet requestCount={wxpayAccountRequestGetCount}
        responseDataHandle={wxpayAccountGetResponse}></WxpayAccountRequestGet>
      <ProductsList
        productsData={wxpayAccountData}
        loadMore={loadMore}
        hasMore={hasMoreState}
        ItemRendererComponent={CurrentItemRendererComponent}></ProductsList>

      <UpdateWxpayAccount
        visible={updateWxpayAccountVisible}
        setVisible={setUpdateWxpayAccountVisible}
        oldObj={selectedObj}
        reloadListCount={reloadListCount}
        setReloadListCount={setReloadListCount}
      ></UpdateWxpayAccount>
    </div>
  );
}

WxpayAccountList.defaultProps = {
  reloadListCount: 1,
  setReloadListCount: (addedNum) => { console.log(addedNum); },
  requestParams: { page: 0 }
}

export default WxpayAccountList;
